import { Box, Button, Flex, Link, IconButton, Image, Menu, MenuButton, MenuItem, MenuList } from '@chakra-ui/react';
import React from 'react';
import img from "../../src/Images/img.png";
import "../Styles/Navbar.css";
import { ChevronDownIcon, DownloadIcon, HamburgerIcon } from '@chakra-ui/icons';
import { Link as Links } from 'react-scroll';
import Resume from "../Images/Himanku_Gogoi_Resume.pdf";

const Navbar = () => {
  const [click,setClick] = React.useState(true);
  const closeMenu = () => {
    setClick(true);
  }
  return (
    <Flex className="box_shadow" justifyContent="space-around" >
        <Box>
            <Image src={img} w={{base:"40%",sm:"40%",md:"35%",lg:"40%"}}/>
        </Box>
        <Flex className={click? "nav-menu active" : "nav-menu"} w={{md:"100%",lg:"65%"}} fontSize={{md:"16px",lg:"19px"}} fontWeight="bold" display={{base:"none", sm:"none", md:"flex", lg:"flex"}}>
            <Flex onClick={closeMenu} classname="nav-item"><Links to='about' spy={true} smooth={true} offset={-50} duration={500}>About Me</Links></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Links to='skills' spy={true} smooth={true} offset={-50} duration={500}>Skills</Links></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Links to='projects' spy={true} smooth={true} offset={-50} duration={500}>Projects</Links></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Links to='github' spy={true} smooth={true} offset={-100} duration={500}>GitHub Stats</Links></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Links to='contact' spy={true} smooth={true} offset={-50} duration={500}>Contact Me</Links></Flex>
            <Menu>
            {({ isOpen }) => (
              <>
                <MenuButton colorScheme='orange' size={{base:"sm",md:"sm",lg:"md"}} isActive={isOpen} as={Button} rightIcon={<ChevronDownIcon />}>
                Resume
                </MenuButton>
                <MenuList>
                <a target="_blank" rel="noreferrer" href="https://drive.google.com/file/d/15o6A0qrMaHGVjzG8veE7v3y1DiYtCIvr/view?usp=share_link"><MenuItem>View Resume</MenuItem></a>
                  <MenuItem><Link textDecoration="none" href={Resume} download="Himanku_Gogoi_Resume.pdf">Download Resume</Link></MenuItem>
                </MenuList>
              </>
            )}
          </Menu>
           
        </Flex>
            <Menu>
              <MenuButton
                  display={{base:"block", sm:"block", md:"none", lg:"none"}}
                  as={IconButton}
                  aria-label='Options'
                  icon={<HamburgerIcon />}
                  variant='outline'
              />
              <MenuList >
                  <MenuItem>
                    <Links to='about' spy={true} smooth={true} offset={-50} duration={500}>About Me</Links>
                  </MenuItem>
                  <MenuItem >
                    <Links to='skills' spy={true} smooth={true} offset={-50} duration={500}>Skills</Links>
                  </MenuItem>
                  <MenuItem >
                    <Links to='projects' spy={true} smooth={true} offset={-50} duration={500}>Projects</Links>
                  </MenuItem>
                  <MenuItem >
                  <Links to='github' spy={true} smooth={true} offset={-100} duration={500}>GitHub Stats</Links>
                  </MenuItem>
                  <MenuItem >
                  <Links to='contact' spy={true} smooth={true} offset={-50} duration={500}>Contact Me</Links>
                  </MenuItem>
                  <MenuItem color="rgba(219, 101, 5, 0.801)" fontWeight={600}>
                    <Link textDecoration="none" href={Resume} download="Himanku_Gogoi_Resume.pdf">Download Resume <DownloadIcon/></Link>
                  </MenuItem>
              </MenuList>
          </Menu>
    </Flex>
  )
}

export default Navbar